import React, { Component } from "react";

class TheMatrix extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showing: true,
    };
    this.showDot = this.showDot.bind(this);
  }

  showDot() {
    setInterval(() => {
      this.showDot();
    }, 900);
    this.setState({
      showing: !this.state.showing,
    });
  }

  componentWillMount() {
    this.showDot();
  }

  render() {
    return (
      <h3
        style={
          (!this.state.showing && { opacity: 0 }) ||
          (this.state.showing && { opacity: 1 })
        }
        onClick={this.showDot}
        className="App-h3"
      >
        The Matrix has you...
      </h3>
    );
  }
}

export default TheMatrix;
