import React, { Component } from "react";
import "./Todo.css";

class Todo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      active: false,
      isEditing: false,
      newInput: this.props.todoItem,
    };

    this.toggleForm = this.toggleForm.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleUpdate = this.handleUpdate.bind(this);
    this.handleDelete = this.handleDelete.bind(this);
    this.handleScratch = this.handleScratch.bind(this);
  }
  handleUpdate(evt) {
    evt.preventDefault();
    this.props.editTodo(this.props.id, this.state.newInput);
  }

  handleScratch() {
    this.props.scratchTodo(this.props.id);
  }

  handleDelete() {
    this.props.deleteTodo(this.props.id);
  }

  handleChange(evt) {
    this.setState({
      [evt.target.name]: evt.target.value,
    });
  }

  toggleForm() {
    this.setState({
      isEditing: !this.state.isEditing,
    });
  }

  render() {
    let result;
    if (this.state.isEditing) {
      result = (
        <div className="update-form">
          <form onSubmit={this.handleUpdate}>
            <input
              name="newInput"
              value={this.state.newInput}
              onChange={this.handleChange}
              type="text"
            />
            <button className="Todo-edit-submit">Submit</button>
          </form>
          <button onClick={this.toggleForm}>Back</button>
        </div>
      );
    } else {
      return (
        <div className="Todo">
          <h4
            onClick={this.handleScratch}
            className={`Todo-h3 ${this.props.active && `Todo-Not-Active`}`}
          >
            {this.props.todoItem}
          </h4>{" "}
          <br />
          <div className="Todo-buttons">
            <button onClick={this.toggleForm}>Edit</button>
            <button onClick={this.handleDelete}>X</button>
          </div>
        </div>
      );
    }
    return result;
  }
}

export default Todo;
