import React, { Component } from "react";
import Todo from "./Todo";
import NewTodoForm from "./NewTodoForm";
import { v4 as uuid } from "uuid";
import "./Todo.css";

class TodoList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      todos: [
        // {
        //   active: false,
        //   id: "a3d6a1fd-3ead-44c5-a2e2-d1a0826cc2c7",
        //   todo: "Find Job",
        // },
        // {
        //   active: false,
        //   id: "a3d6a1fd-33ad-44c5-a2e2-d1a0826cc2c7",
        //   todo: "Learn React",
        // },
      ],
      display: false,
    };
    this.newTodo = this.newTodo.bind(this);
    this.editTodo = this.editTodo.bind(this);
    this.deleteTodo = this.deleteTodo.bind(this);
    this.handleState = this.handleState.bind(this);
    this.saveToLocal = this.saveToLocal.bind(this);
    this.getFromLocal = this.getFromLocal.bind(this);
    this.scratchTodo = this.scratchTodo.bind(this);
  }

  componentWillMount() {
    this.getFromLocal();
  }

  saveToLocal() {
    localStorage.setItem("state", JSON.stringify(this.state.todos));
  }

  getFromLocal() {
    this.setState({
      todos: JSON.parse(localStorage.getItem("state")),
    });
  }

  handleState() {
    this.setState({ display: !this.state.display });
  }

  editTodo(id, updatedTask) {
    const updatedTodo = this.state.todos.map((todo) => {
      if (todo.id === id) {
        return { ...todo, todo: updatedTask };
      }
      return todo;
    });
    this.setState({
      todos: updatedTodo,
    });
    this.saveToLocal();
  }

  async scratchTodo(id) {
    const updatedTodo = this.state.todos.map((todo) => {
      if (todo.id === id) {
        return { ...todo, active: !todo.active };
      }
      return todo;
    });
    await this.setState({
      todos: updatedTodo,
    });
    await this.saveToLocal();
  }

  async deleteTodo(id) {
    await this.setState({
      todos: this.state.todos.filter((todo) => todo.id !== id),
    });
    await this.saveToLocal();
  }

  async newTodo(newState) {
    console.log(newState);

    await this.setState({
      todos: [...this.state.todos, newState],
    });
    await localStorage.setItem("state", JSON.stringify(this.state.todos));
  }

  render() {
    return (
      <div>
        <div
          style={this.state.display ? { opacity: 1 } : { opacity: 0 }}
          className="Todo-screen"
        >
          {" "}
          <div className="TodoList">
            <div className="TodoList-todos">
              {" "}
              {this.state.todos.map((todo) => (
                <Todo
                  active={todo.active}
                  key={uuid()}
                  id={todo.id}
                  editTodo={this.editTodo}
                  deleteTodo={this.deleteTodo}
                  todoItem={todo.todo}
                  scratchTodo={this.scratchTodo}
                />
              ))}
            </div>
          </div>{" "}
          <NewTodoForm newTodo={this.newTodo} />{" "}
          <button className="TurnOnBtn" onClick={this.handleState}>
            {" "}
            <i className="fa fa-power-off"></i>
          </button>
        </div>
      </div>
    );
  }
}

export default TodoList;
